/* Global Styles */
:root {
  --primary-color: #3498db;
  --secondary-color: #2ecc71;
  --background-color: #f4f4f9;
  --text-color: #333;
  --font-family-main: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  --font-family-code: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  --transition-speed: 0.3s ease-in-out;
}

body {
  margin: 0;
  font-family: var(--font-family-main);
  color: var(--text-color);
  background-color: var(--background-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.6;
  min-height: 100vh; /* Ensures content fills the viewport */
  display: flex;
  flex-direction: column;
}

/* Task List Styles */
.task-list {
  flex: 1;
  padding: 20px;
}

.task-item {
  display: flex;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.task-item input {
  margin-right: 10px;
}

.task-item span {
  flex: 1;
  margin-right: 10px;
}

.task-item .completed {
  text-decoration: line-through;
  color: #888;
}

.delete-button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
}

/* Form Styles */
.task-form {
  padding: 20px;
  background-color: #f8f8f8;
}

/* Responsive Design */
@media (max-width: 768px) {
